export const CartEmpty: React.FC = () => {
  return (
    <>
      <div className='cart cart--empty'>
        <h2>
            <span></span>
        </h2>
        <p>
           ,     .
          <br />
           ,   ,    .
        </p>
        <img src={cartEmptyImg} alt='Empty cart' />
        <Link to='/' className='button button--black'>
          <span> </span>
        </Link>
      </div>
    </>
  );
};
